import axios from "axios";
import { mapActions } from "vuex";

export default {
  data() {
    return {
      url: "",
      method: "get",
      data: JSON.stringify({}),
      dialog: false,
      email: "",
      password: "",
      error: false,
    };
  },

  computed: {
    error() {
      return this.$store.getters.error;
    },
  },

  methods: {
    ...mapActions(["loadUserDataFromServer", "setUser", "setFlagLogin"]),

    getDataFromApi() {
      var config = {
        method: this.method,
        url: this.url,
        headers: {
          "Content-Type": "application/json",
        },
        auth: {
          username: "user",
          password: this.$store.getters.token,
        },
        data: this.data,
      };

      return axios(config);
    },

    onSignin() {
      this.method = "get";
      this.url = "/user/finduser/" + this.email + "/" + this.password;
      this.data = JSON.stringify({});
      this.getDataFromApi().then((data) => {
        if (data.data == "") {
          this.error = true;
          return;
        } else {
          this.error = false;
          this.email = null;
          this.password = null;
          this.dialog = false;
          this.setUser(data.data);
          this.setFlagLogin(true);
          this.loadUserDataFromServer(data.data.id);
        }
      });
    },

    close() {
      this.error = false;
      this.email = null;
      this.password = null;
      this.dialog = false;
    },

    onDismissed() {
      console.log("Dismissed Alert");
      this.error = false;
    },
  },
};
